@mixin content-search {
  .search-title {
    margin-bottom: $lem;

    span {
      @include font(bold);
    }
  }

  .article {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .article-title,
    .article-permalink,
    .article-date {
      padding: $lem;
    }
  }
}
