@mixin content-single {
  .article {
    @include article-grid--display-vertical;

    &.display-horizontal {
      @include article-grid--display-vertical;

      .article-featured-image {
        @include article-featured-image--without-border;
      }
    }

    .article-permalink {
      display: none;
    }
  }

  .article-footer {
    @include article-footer;
  }
}
