@mixin article-grid {
  @include border(normal, all);

  // On small screens all articles will be displayed vertically
  // On bigger screens the display will be decided by the post format
  @include mobile {
    @include article-grid--display-vertical;
  }

  .article-title,
  .article-meta,
  .article-excerpt,
  .article-content,
  .article-paginated-content,
  .article-footer {
    padding: $lem;
  }

  + .article {
    margin-top: $lem;
  }
}
