@mixin article {
  @include colors(default);
  @include article-grid;

  .article-sticky {
    @include article-sticky;
  }

  .sticky {
    // Needed by WP Theme check ...
    @include colors(default);
  }

  .article-title {
    @include article-title;
  }

  .article-date {
    @include article-date;
  }

  .article-date-and-time {
    @include article-date-and-time;
  }

  .article-author {
    @include article-author;
  }

  .article-permalink {
    @include article-permalink;
  }

  .article-featured-image {
    @include article-featured-image;
  }

  .article-excerpt {
    @include article-excerpt;
  }

  .article-content {
    @include article-content;
  }

  .article-paginated-content {
    @include article-paginated-content;
  }

  &.post-format-link {
    @include post-format-link;
  }

  &.post-format-image {
    @include post-format-image;
  }

  &.post-format-gallery {
    @include post-format-gallery;
  }

  &.post-format-audio {
    @include post-format-audio;
  }

  &.post-format-video {
    @include post-format-video;
  }

  &.post-format-aside {
    @include post-format-aside;
  }

  &.post-format-status {
    @include post-format-status;
  }

  &.post-format-quote {
    @include post-format-quote;
  }

  &.post-format-chat {
    @include post-format-chat;
  }

  &.post-format-standard {
    @include post-format-standard;
  }
}
