@mixin article-footer {
  display: flex;
  flex-direction: column;

  @include border(normal, all);
  border-top: none;

  @include colors(default);


  .date-and-author {
    display: flex;
    justify-content: space-between;
  }

  .categories-and-tags {
    display: flex;

    @include mobile {
      flex-direction: column;

      .article-tags {
        margin-top: $lem;
      }
    }

    @include mobile-and-up {
      .article-categories + .article-tags {
        margin-left: $lem;
      }
    }

    @include tablet {
      .article-categories,
      .article-tags {
        min-width: 30%;
      }
    }
  }

  .categories-and-tags,
  .date-and-author,
  .article-edit-link {
    padding: $lem;
  }

  .date-and-author + .categories-and-tags,
  .date-and-author + .article-edit-link,
  .categories-and-tags + .article-edit-link {
    padding-top: 0;
  }

  .link,
  a {
    @include link;
  }
}
