@mixin article-content {
  .text {
    @include text-style(body-text);
    @include text-style(body-text-article);
    max-width: $text-block-width;

    // to clear floats for an <img> inside
    // - https://www.quirksmode.org/css/clearing.html
    width: 100%;
    overflow: hidden;

    // Gallery support
    .gallery {
      @include article-content-gallery;
    }

    // Arrows for read more ...
    .arrow-with-triangle {
      @include arrow-with-triangle;
      margin-left: $lem / 2;
    }

    .more-link {
      display: flex;
    }
  }
}
