@mixin post-format-standard {
  @include mobile-and-up {
    @include article-grid--display-vertical;

    &.display-horizontal {
      @include article-grid--display-horizontal;

      .article-featured-image {
        @include article-featured-image--with-border;
      }
    }
  }

  @include portrait {
    &.has-excerpt.has-thumbnail {
      @include article-grid--display-vertical;

      .article-featured-image {
        @include article-featured-image--without-border;
      }
    }
  }

  // Styling attachments post formats
  // - we simply can't do it elsewhere  like in a `post-format-attachment` ...
  .article-content {
    .attachment {
      img {
        @include border(normal, all);
        padding: $lem;
      }
    }
  }
}
